  /**/
  public accountLineNum(k: number , x: number) {
    let X = [];
    let Y = [];
    if (k && x) {
       X = [0, 0];
       Y = [x, Math.round(k * x)];
    } else {
      X = [0, 0];
      Y = [15, Math.round(k * 4)];
    }
    return [X, Y];
  }
